.x-at-nav {
  display: flex;
  height: 1.2rem;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.05);
  background: #fff;
}

.x-at-nav > .x-at-navitem {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.x-at-nav > .x-at-navitem > i {
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 0.5rem;
  margin-bottom: 0.06rem;
}

.x-at-nav > .x-at-navitem > small {
  color: #5a5a6e;
  font-size: 0.2rem;
}

.x-at-nav > .x-at-navitema:active {
  opacity: 0.7;
}

.x-at-nav p.x-at-navitem small {
  color: #00cf97;
}

.x-at-nav p.x-at-navitem__index i {
  background-image: url("./icon-home-active.png");
}

.x-at-nav p.x-at-navitem__collection i {
  background-image: url("./icon-collection-active.png");
}

.x-at-nav p.x-at-navitem__order i {
  background-image: url("./icon-order-active.png");
}

.x-at-nav p.x-at-navitem__user i {
  background-image: url("./icon-user-active.png");
}

.x-at-nav a.x-at-navitem__index i {
  background-image: url("./icon-home.png");
}

.x-at-nav a.x-at-navitem__collection i {
  background-image: url("./icon-collection.png");
}

.x-at-nav a.x-at-navitem__order i {
  background-image: url("./icon-order.png");
}

.x-at-nav a.x-at-navitem__user i {
  background-image: url("./icon-user.png");
}