.x-input {
  display: flex;
  height: 0.9rem;
  width: 100%;
  border: 1px solid #ebebee;
  border-radius: 0.06rem;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out;
  background: #fff;
  overflow: hidden;
}

.x-input:focus-within {
  border: 1px solid #c4c4cd;
}

.x-input--mini {
  height: 0.7rem;
}

.x-input--multi {
  height: 1.75rem;
}

.x-input .x-input__ipt {
  font-size: 0.3rem;
  display: block;
  flex: 1 1;
  width: 100%;
  height: 100%;
  line-height: 1.5;
  color: #3c3c5a;
  padding: 0.2rem;
  border: none;
  font-weight: normal;
  resize: none;
}

.x-input .x-input__ipt::-webkit-input-placeholder {
  color: #b4b4b4;
}

.x-input--disabled .x-input__ipt {
  color: #b4b4b4 !important;
}

.x-input.x-input--error {
  border: 1px solid #ff5949;
}

.x-input.x-input--error .x-input__ipt {
  color: #ff5949 !important;
}

.x-input__addon-before,
.x-input__addon-after {
  display: flex;
  align-items: center;
  height: 100%;
  font-size: 0.3rem;
}

.x-input__addon-before > *,
.x-input__addon-after > * {
  height: 100%;
  display: flex;
  align-items: center;
}

.x-input [type='date']::-webkit-clear-button {
  display: none;
}

.x-input [type='date']::-webkit-inner-spin-button {
  display: none;
}

.x-input [type='date']::-webkit-datetime-edit-text {
  color: #ddd;
}

.x-input [type='date']::-webkit-calendar-picker-indicator {
  background: rgba(0, 0, 0, 0);
  color: #ff5949;
  height: 0.5em;
}