html,
body,
#root {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

* {
  color: #3c3c5a;
}

a,
button {
  color: #00cf97;
  border: none;
  background: transparent;
  outline: none;
  padding: 0;
  display: inline;
  -webkit-appearance: none;
}

a:active,
button:active {
  opacity: 0.7;
}

.x-app {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  background: #fff;
}

.x-app-header {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
}

.x-app-header.x-app-header--bottom-shadow {
  box-shadow: 0 0 0.2rem rgba(0, 0, 0, 0.08);
}

.x-app-header.x-app-header--bottom-border {
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05);
}

.x-app-header.x-app-header--has-tabs > .x-tabs {
  margin-top: -0.2rem;
  position: relative;
  z-index: 4;
}

.x-app-header.x-app-header--has-tabs.x-app-header--headline > .x-tabs {
  margin-top: -0.4rem;
}

.x-app-header:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  -webkit-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
}

.x-app-header__inner {
  position: relative;
  z-index: 2;
  flex: 1 1;
  display: flex;
  align-items: center;
  min-height: 1.1rem;
}

.x-app-header__inner a:not([class]),
.x-app-header__inner button:not([class]) {
  color: #8a8a9c;
}

.x-app-header__title {
  display: block;
  text-align: center;
  width: 100%;
  padding: 0 1rem;
  font-size: 0.36rem;
  color: #3e3d57;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-weight: normal;
}

.x-app-header__addon-before,
.x-app-header__addon-after {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  display: flex;
  flex: 1 1;
  align-items: center;
  z-index: 2;
  padding: 0 0.4rem;
}

.x-app-header__addon-before *,
.x-app-header__addon-after * {
  pointer-events: auto;
}

.x-app-header__addon-after {
  justify-content: flex-end;
}

.x-app-header__homepage-button {
  display: block;
  width: 0.8rem;
  height: 0.8rem;
  background: url("data:image/png;base64,") center no-repeat;
  background-size: 0.33rem;
  margin-right: -0.23rem;
}

.x-app-header__addon-bottom {
  position: relative;
  z-index: 3;
}

.x-app-header__addon-bottom > .x-tabs {
  margin-top: -0.3rem;
}

.x-app-header--ghost {
  box-shadow: none;
}

.x-app-header--ghost:after {
  opacity: 0;
}

.x-app-header--ghost .x-app-header__title {
  color: #fff;
}

.x-app-header--ghost .x-app-header__inner a:not([class]),
.x-app-header--ghost .x-app-header__inner button:not([class]) {
  color: #fff;
}

.x-app-header__back {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 0.8rem;
  height: 0.8rem;
  margin-left: -0.25rem;
}

.x-app-header__back svg {
  width: 0.36rem;
  height: 0.36rem;
}

.x-app-header__back path {
  fill: #3c3c5a;
}

.x-app-header__back:active {
  opacity: 0.7;
}

.x-app-header__close {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 0.8rem;
  height: 0.8rem;
  margin-left: -0.2rem;
}

.x-app-header__close svg {
  width: 0.36rem;
  height: 0.36rem;
}

.x-app-header__close path {
  fill: #3c3c5a;
}

.x-app-header__close:active {
  opacity: 0.7;
}

.x-app-header--ghost .x-app-header__back path {
  fill: #fff;
}

.x-app-header--ghost .x-app-header__close path {
  fill: #fff;
}

.x-app-header--headline .x-app-header__addon-before,
.x-app-header--headline .x-app-header__addon-after {
  height: 1.1rem;
}

.x-app-header--headline .x-app-header__inner {
  min-height: 0;
  padding: 1rem 0 0.4rem 0;
}

.x-app-header--headline .x-app-header__title {
  text-align: left;
  padding: 0 0.4rem;
  font-size: 0.55rem;
  font-weight: bold;
}

.x-app-header--headline .x-app-header__addon-bottom {
  margin-top: -0.35rem;
  padding: 0 0.4rem 0.4rem 0.4rem;
  color: #8a8a9c;
}

.x-app-header--headline-fix-padding .x-app-header__inner {
  padding-top: 0.4rem;
}

.x-app-header--headline-fix-padding .x-app-header__addon-after {
  height: 1.55rem;
}

.x-app-body {
  width: 100%;
  flex: 1 1;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  position: relative;
  padding: 0.4rem;
  z-index: 1;
}

.x-app-body__inner > .x-cell {
  margin-left: -0.4rem;
  margin-right: -0.4rem;
}

.x-app-body__inner > .x-cell__title:first-child {
  margin-top: 0;
}

.x-app-body--loading .x-app__loading {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 0.6rem;
  height: 0.6rem;
  margin: -0.3rem 0 0 -0.3rem;
}

.x-app-body--loading .x-loading__spin {
  width: 100%;
  height: 100%;
}

.x-app-body--loading .x-loading__spin_path {
  stroke-width: 2px;
}

.x-app-body--error .x-app__error-info {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 40%;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
  color: #c4c4cd;
  font-size: 0.26rem;
  padding: 0 1rem;
}

.x-app-body--error .x-app__error-info svg {
  width: 0.8rem;
  height: 0.8rem;
  margin-bottom: 0.2rem;
}

.x-app-body--error .x-app__error-info path {
  fill: #c4c4cd;
}

.x-app-body__bottom {
  margin-top: 0.2rem;
  text-align: center;
  color: #c4c4cd;
}

.x-app-body__bottom-inner {
  height: 0.8rem;
  line-height: 0.8rem;
}

.x-app-body__bottom-inner:active {
  opacity: 0.7;
}

.x-app-footer {
  background: #fff;
  min-height: 0.9rem;
  position: relative;
  z-index: 2;
}