.x-card {
  border-radius: 0.2rem;
  background: #fff;
  overflow: hidden;
}

.x-card__inner {
  display: flex;
}

.x-card--ar {
  position: relative;
}

.x-card--ar .x-card__inner {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.x-card__ar {
  pointer-events: none;
  width: 100%;
}

.x-card--shadow {
  box-shadow: 0 0.2rem 0.6rem rgba(0, 0, 0, 0.1);
}

.x-card--column {
  flex-direction: column;
}

.x-card--column-reverse {
  flex-direction: column-reverse;
}

.x-card--row {
  flex-direction: row;
}

.x-card--row-reverse {
  flex-direction: row-reverse;
}

.x-card--activable {
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.x-card--activable:active {
  -webkit-transform: scale(0.99);
  transform: scale(0.99);
  opacity: 0.8;
}

.x-card__figure {
  position: relative;
  overflow: hidden;
  border-radius: 0.04rem;
  flex: none;
}

.x-card__figure--activable:active > img {
  opacity: 0.7;
}

.x-card__figure > img {
  display: block;
  width: 100%;
  height: 100%;
}

.x-card__figure .x-card__figure-abs-img {
  position: absolute;
  left: 0;
  top: 0;
}

.x-card__figure-content {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9;
}