.at-calendar {
  background: #fff;
}

.at-calendar .header .time-range {
  display: flex;
  justify-content: space-between;
  padding: 0 0.4rem;
  margin-bottom: 0.7rem;
  background: url("./icon-range.png") center center no-repeat;
  background-size: 0.6rem 0.6rem;
}

.at-calendar .header .time-range .date {
  color: #3c3c5a;
}

.at-calendar .header .time-range .date.active {
  color: #00cf97;
}

.at-calendar .header .time-range .date h6 {
  color: inherit;
  font-size: 0.4rem;
  margin-bottom: 0.2rem;
  font-weight: normal;
}

.at-calendar .header .time-range .date p {
  font-size: 0.3rem;
}

.at-calendar .header .time-range .date.to p {
  text-align: right;
}

.at-calendar .header .weeks {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 0.4rem;
  margin-bottom: 0.2rem;
}

.at-calendar .clear {
  color: #8a8a9c;
}

.at-calendar .month .month-title {
  font-size: 0.34rem;
  font-weight: 400;
  line-height: 0.4rem;
  margin-bottom: 0.7rem;
}

.at-calendar .month > .list {
  overflow: hidden;
}

.at-calendar .month .month-day {
  float: left;
  width: 14.285%;
  margin-bottom: 0.6rem;
}

.at-calendar .month .month-day.disabled p em {
  color: #b2b2b2;
  background: none;
}

.at-calendar .month .month-day.disabled span {
  visibility: hidden;
}

.at-calendar .month .month-day.disabled-all p em {
  color: #b2b2b2;
  background: none;
}

.at-calendar .month .month-day.disabled-all p:before {
  content: '';
  position: absolute;
  left: 15%;
  top: 50%;
  width: 70%;
  height: 1px;
  background: #b2b2b2;
  -webkit-transform: translate(0, -50%) rotate(45deg);
  transform: translate(0, -50%) rotate(45deg);
}

.at-calendar .month .month-day.disabled-all span {
  visibility: hidden;
}

.at-calendar .month .month-day.disabled-part p em {
  background: url("./date-part.png");
  background-size: 100% 100%;
}

.at-calendar .month .month-day p {
  padding: 0 0.15rem;
  display: block;
  position: relative;
  overflow: hidden;
  margin-bottom: 0.2rem;
}

.at-calendar .month .month-day p:after {
  content: '';
  display: block;
  margin-top: 100%;
}

.at-calendar .month .month-day em {
  position: absolute;
  background-size: 100%;
  left: 0.15rem;
  top: 0;
  right: 0.15rem;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-style: normal;
  font-size: 0.22rem;
  color: #3c3c5a;
  z-index: 2;
  background: #f2f2f2;
  border-radius: 50%;
}

.at-calendar .month .month-day span {
  font-size: 0.2rem;
  color: #c4c4cd;
  display: block;
  text-align: center;
  height: 0.2rem;
  line-height: 0.2rem;
}

.at-calendar .month .month-day.active p em {
  color: #fff;
  background: none;
}

.at-calendar .month .month-day.active p:before {
  content: '';
  display: block;
  position: absolute;
  left: -1px;
  right: -1px;
  top: 0;
  bottom: 0;
  background: #00cf97;
  -webkit-transform: translate(0, 0) rotate(0);
  transform: translate(0, 0) rotate(0);
  height: 100%;
  width: auto;
}

.at-calendar .month .month-day.empty + .active p:before,
.at-calendar .month .month-day.active:first-child p:before,
.at-calendar .month .month-day.active.mon p:before,
.at-calendar .month .month-day.active-first p:before {
  left: 50%;
}

.at-calendar .month .month-day.empty + .active em,
.at-calendar .month .month-day.active:first-child em,
.at-calendar .month .month-day.active.mon em,
.at-calendar .month .month-day.active-first em {
  background: #00cf97 !important;
  border-radius: 50%;
  color: #fff !important;
}

.at-calendar .month .month-day.active-first.disabled-all:not(.active) p:before {
  display: none;
}

.at-calendar .month .month-day.active:last-child p:before,
.at-calendar .month .month-day.active.sun p:before,
.at-calendar .month .month-day.active-end p:before {
  right: 50%;
}

.at-calendar .month .month-day.active:last-child em,
.at-calendar .month .month-day.active.sun em,
.at-calendar .month .month-day.active-end em {
  background: #00cf97 !important;
  border-radius: 50%;
  color: #fff;
}

.at-calendar .month .month-day.active-first em:before,
.at-calendar .month .month-day.active-end em:before {
  display: block;
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  border: 2px solid #00a679;
  border-top-color: transparent;
  border-right-color: transparent;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.at-calendar .month .month-day.active-end em:before {
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
}

.at-calendar .month .month-day.active-first.active-end em:before {
  border-top-color: #00a679 !important;
  /*no*/
  border-right-color: #00a679 !important;
  /*no*/
  border-bottom-color: #00a679 !important;
  /*no*/
  border-left-color: #00a679 !important;
  /*no*/
  -webkit-transform: rotate(0);
  transform: rotate(0);
}

.at-calendar .month .month-day.is-holiday span {
  color: #ff5949;
}

.at-calendar .footer {
  box-shadow: 0 0 0.3rem rgba(0, 0, 0, 0.08);
  min-height: 0;
}

.at-calendar .footer .tips {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0.2rem 0.4rem;
}

.at-calendar .footer .tips > p {
  display: flex;
  align-items: center;
  font-size: 0.24rem;
  color: #8a8a9c;
  height: 0.24rem;
  line-height: 0.24rem;
  margin-left: 0.4rem;
}

.at-calendar .footer .tips > p i {
  width: 0.3rem;
  height: 0.3rem;
  margin-right: 0.1rem;
  display: block;
  background: #ddd;
  border-radius: 50%;
}

.at-calendar .footer .tips > p i.part {
  background: url("./icon-date-part.png");
  background-size: 100% 100%;
}

.at-calendar .footer .bottom {
  display: flex;
  align-items: center;
  padding: 0.2rem 0.4rem;
  border-top: 1px solid #f2f2f2;
}

.at-calendar .footer .bottom > p {
  display: block;
  flex: 1 1;
  font-size: 0.28rem;
  color: #3c3c5a;
}

.at-calendar .footer .bottom > p strong {
  font-size: 0.34rem;
  padding-left: 0.1rem;
}

.at-calendar .footer .bottom .submit {
  width: 2.4rem;
  flex: none;
}

.at-calendar__time-picker .x-popup__inner {
  overflow: hidden;
}

.at-calendar__time-picker .header {
  display: flex;
  height: 1rem;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.3rem;
}

.at-calendar__time-picker .header a {
  font-size: 0.26rem;
}

.at-calendar__time-picker .header a.cancel {
  color: #8a8a9c;
}

.at-calendar__time-picker .x-time-picker {
  padding-left: 2rem;
  padding-right: 2rem;
  overflow: visible;
}

.at-calendar__time-picker .x-time-picker__day-list {
  display: none;
}

.at-calendar__time-picker .x-time-picker__tips {
  margin-top: -0.2rem;
}