.x-tabs {
  display: flex;
  height: 1rem;
  position: relative;
  width: 100%;
}

.x-tabs__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  flex: 1 1;
  background: #fff;
  font-size: 0.3rem;
  color: #3e3d57;
  position: relative;
}

.x-tabs__item:active {
  color: inherit;
}

.x-tabs--active {
  color: #00cf97;
  font-weight: bold;
}

.x-tabs--active:active {
  color: #00cf97;
  opacity: 1;
}

.x-tabs__line {
  width: 50%;
  height: 0.06rem;
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  display: block;
  position: absolute;
  left: 0;
  bottom: 0.1rem;
  z-index: 9;
  top: auto;
  pointer-events: none;
}

.x-tabs__line:after {
  content: '';
  background: -webkit-linear-gradient(right, #ffd100, rgba(255, 209, 0, 0));
  background: linear-gradient(to left, #ffd100, rgba(255, 209, 0, 0));
  width: 0.6rem;
  height: 0.06rem;
  position: absolute;
  left: 50%;
  top: 0;
  margin-left: -0.3rem;
  border-radius: 0.03rem;
}

.x-tabs__line--ani:after {
  -webkit-animation: __x_tabs_line_scale 0.3s ease-out;
  animation: __x_tabs_line_scale 0.3s ease-out;
}

@-webkit-keyframes __x_tabs_line_scale {
  0% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }

  50% {
    -webkit-transform: scaleX(4);
    transform: scaleX(4);
  }

  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}

@keyframes __x_tabs_line_scale {
  0% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }

  50% {
    -webkit-transform: scaleX(4);
    transform: scaleX(4);
  }

  100% {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
}