.x-cell {
  border-top: 1px solid #ebebee;
  border-bottom: 1px solid #ebebee;
  background: #fff;
  overflow: hidden;
  min-height: 1rem;
}

.x-cell__row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 0.2rem 0.4rem;
  min-height: 1rem;
  font-size: 0.3rem;
  color: #3c3c5a;
  position: relative;
  border-bottom: 1px solid #ebebee;
}

.x-cell__row--indent-line,
.x-cell__row--end-indent-line {
  border-bottom: none;
}

.x-cell__row--indent-line::after,
.x-cell__row--end-indent-line::after {
  content: '';
  display: block;
  height: 1px;
  background: #ebebee;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.x-cell__row--indent-line:last-child:after,
.x-cell__row--end-indent-line:last-child:after {
  display: none;
}

.x-cell__row--indent-line::after {
  left: 0.4rem;
}

.x-cell__row--end-indent-line::after {
  right: 0.4rem;
}

.x-cell__row > label {
  font-size: 0.3rem;
}

.x-cell__row:last-child {
  border-bottom: none;
}

.x-cell__row--arrow {
  padding-right: 0.6rem;
}

.x-cell__row--arrow::before {
  content: '';
  display: block;
  width: 0.18rem;
  height: 0.18rem;
  position: absolute;
  pointer-events: none;
  right: 0.25rem;
  top: 50%;
  background: url("data:image/png;base64,") right center no-repeat;
  background-size: auto 100%;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
}

.x-cell__row--activable:active {
  opacity: 1;
  background: #f9f9fa;
}

.x-cell__title {
  display: flex;
  align-items: center;
  margin-top: 0.3rem;
}

.x-cell__title > h2 {
  font-size: 0.3rem;
  font-weight: normal;
  color: #8a8a9c;
  line-height: 0.6rem;
}