@charset "utf-8";

body {
  font-family: Helvetica;
}

html,
body,
body div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

article,
aside,
figure,
footer,
header,
hgroup,
nav,
section {
  display: block;
}

/* force a vertical scrollbar to prevent a jumpy page */

body {
  display: block;
}

/* we use a lot of ULs that aren't bulleted.
	don't forget to restore the bullets within content. */

ul,
menu {
  list-style: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

/* tables still need cellspacing="0" in the markup */

table {
  border-collapse: collapse;
  border-spacing: 0;
}

th {
  font-weight: bold;
  vertical-align: bottom;
}

td {
  font-weight: normal;
  vertical-align: top;
}

/* let's clear some floats */

.clearfix:before,
.clearfix:after {
  content: ' ';
  display: block;
  height: 0;
  overflow: hidden;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  zoom: 1;
}

/* new Linux- and Windows-friendly sans-serif font stack: http://mky.be/fontstack */

body {
  font-size: 0.3rem;
  color: rgb(60, 60, 90);
  line-height: 1.5;
}

* {
  font-size: inherit;
  color: inherit;
  line-height: inherit;
}

a {
  color: #00bc93;
}

a:active {
  opacity: 0.7;
}

/* 去除默认皮肤样式，默认事件等 */

* {
  -webkit-appearance: none;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none;
  -webkit-touch-callout: none;
}

/* 切换盒模型 */

*,
*:after,
*:before {
  box-sizing: border-box;
}
.x-modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
}

.x-modal__mask {
  background: rgba(0, 0, 0, 0.7);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  -webkit-transition: opacity 0.2s ease-out !important;
  transition: opacity 0.2s ease-out !important;
}

.x-modal__inner {
  width: 100%;
  background: #f9f9fa;
  box-shadow: 0 0 0.16rem rgba(0, 39, 61, 0.05);
  position: relative;
  z-index: 2;
}

.x-modal.x-modal--init {
  opacity: 0;
}

.x-modal.x-modal--enter .x-modal__mask {
  opacity: 1 !important;
}

.x-modal.x-modal--leave .x-modal__mask {
  opacity: 0 !important;
}
.x-actionsheet {
  align-items: flex-end !important;
}

.x-actionsheet .x-modal__inner {
  background: #fff;
}

.x-actionsheet__title {
  width: 100%;
  padding: 0.2rem 0;
  min-height: 0.9rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #ebebee;
  background: #fff;
  font-size: 0.26rem;
}

.x-actionsheet__list {
  background: #fff;
}

.x-actionsheet__item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #ebebee;
  font-size: 0.3rem;
}

.x-actionsheet__item-con {
  width: 100%;
  padding: 0.2rem 0.4rem;
}

.x-actionsheet__button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1rem;
  width: 100%;
  color: #3c3c5a;
  font-size: 0.3rem;
}

.x-actionsheet__button:active {
  background: #f9f9fa;
}

.x-actionsheet__closebtn {
  margin-top: 0.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  color: #c4c4cd;
  width: 100%;
  height: 1rem;
  border-top: 1px solid #ebebee;
  font-size: 0.3rem;
}

.x-actionsheet__closebtn:active {
  background: #f9f9fa;
  color: #9d9dac;
}

.x-actionsheet.x-modal--enter .x-modal__bg {
  opacity: 1;
}

.x-actionsheet.x-modal--enter .x-modal__inner {
  -webkit-animation: __x_actionsheet_slideup_enter cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
  animation: __x_actionsheet_slideup_enter cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
}

.x-actionsheet.x-modal--leave .x-modal__bg {
  opacity: 0;
}

.x-actionsheet.x-modal--leave .x-modal__inner {
  -webkit-animation: __x_actionsheet_slideup_leave cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
  animation: __x_actionsheet_slideup_leave cubic-bezier(0.5, 0, 0.3, 1) 0.3s;
}

@-webkit-keyframes __x_actionsheet_slideup_enter {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes __x_actionsheet_slideup_enter {
  0% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }

  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@-webkit-keyframes __x_actionsheet_slideup_leave {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}

@keyframes __x_actionsheet_slideup_leave {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  100% {
    -webkit-transform: translateY(100%);
    transform: translateY(100%);
  }
}
.x-spin {
  display: flex;
  align-items: center;
  justify-content: center;
}

.x-spin__text {
  color: #8a8a9c;
  font-size: 0.28rem;
  margin-left: 0.1rem;
}

.x-loading__spin {
  width: 0.3rem;
  height: 0.3rem;
  overflow: hidden;
  position: relative;
}

.x-loading__spin_circular {
  -webkit-animation: __x_loading_rotate 2s linear infinite;
  animation: __x_loading_rotate 2s linear infinite;
  height: 100%;
  -webkit-transform-origin: center center;
  transform-origin: center center;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.x-loading__spin_path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  stroke: #00cf97;
  stroke-width: 4px;
  -webkit-animation: __x_loading_dash 1.5s ease-in-out infinite;
  animation: __x_loading_dash 1.5s ease-in-out infinite;
  stroke-linecap: round;
}

@-webkit-keyframes __x_loading_rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes __x_loading_rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes __x_loading_dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }

  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}

@keyframes __x_loading_dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }

  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}
.x-button {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  cursor: pointer;
  width: 100%;
  height: 0.9rem;
  font-size: 0.3rem;
  border-radius: 0.06rem;
  white-space: nowrap;
  -webkit-transition: all 0.15s ease-out;
  transition: all 0.15s ease-out;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.x-button:active {
  opacity: 1;
}

.x-button--capsule {
  border-radius: 0.45rem;
}

.x-button .x-button__loading {
  width: 0.25rem;
  height: 0.25rem;
  margin-right: 0.1rem;
}

.x-button .x-button__loading .x-loading__spin_path {
  stroke: #fff;
  stroke-width: 5px;
}

.x-button--default {
  background: #ebebee;
  color: #aaaab0;
}

.x-button--default:active {
  background: #e1e1e4;
  color: #a5a5aa;
  opacity: 1;
}

.x-button--default .x-loading__spin_path {
  stroke: #aaaab0 !important;
}

.x-button--default.x-button--hollow {
  border: 1px solid #ebebee;
  background: #fff;
}

.x-button--default.x-button--hollow:active {
  border: 1px solid #d8d8db;
  background: #ebebee;
}

.x-button--primary {
  background: -webkit-linear-gradient(right, #00cf97, #14d78a);
  background: linear-gradient(to left, #00cf97, #14d78a);
  color: #fff;
}

.x-button--primary:active:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  background: rgba(0, 0, 0, 0.04);
  border-radius: inherit;
}

.x-button--primary.x-button--shadow {
  box-shadow: 0 0.2rem 0.3rem -0.2rem rgba(0, 210, 147, 0.8);
}

.x-button--primary.x-button--hollow {
  border: 1px solid #00cf97;
  background: #fff;
  color: #00cf97;
}

.x-button--primary.x-button--hollow:active {
  border: 1px solid #00be8b;
  color: #00be8b;
  background: rgba(0, 190, 139, 0.1);
}

.x-button--primary.x-button--hollow .x-loading__spin_path {
  stroke: #00cf97;
}

.x-button--danger {
  background: -webkit-linear-gradient(right, #ff5949, #ff4d4d);
  background: linear-gradient(to left, #ff5949, #ff4d4d);
  color: #fff;
}

.x-button--danger:active:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  background: rgba(0, 0, 0, 0.04);
  border-radius: inherit;
}

.x-button--danger.x-button--shadow {
  box-shadow: 0 0.2rem 0.3rem -0.2rem rgba(255, 77, 77, 0.8);
}

.x-button--danger.x-button--hollow {
  border: 1px solid #ff5949;
  background: #fff;
  color: #ff5949;
}

.x-button--danger.x-button--hollow:active {
  border: 1px solid #eb5243;
  color: #eb5243;
  background: rgba(235, 82, 67, 0.1);
}

.x-button--danger.x-button--hollow .x-loading__spin_path {
  stroke: #ff5949;
}

.x-button--mini {
  height: 0.6rem;
  font-size: 0.26rem;
}

.x-button--mini .x-button__loading .x-loading__spin {
  width: 0.2rem;
  height: 0.2rem;
}

.x-button--disabled {
  opacity: 0.3;
  pointer-events: none;
  cursor: not-allowed;
}

.x-button--loading {
  opacity: 0.8;
  pointer-events: none;
  cursor: not-allowed;
}

.x-button--shrink {
  width: auto;
  padding: 0 0.6rem;
  display: inline-flex;
}

.x-button--shrink.x-button--mini {
  padding: 0 0.3rem;
}
.x-alert {
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 99996;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  -webkit-transition: opacity 0.2s ease-out;
  transition: opacity 0.2s ease-out;
}

.x-alert__inner {
  width: 5.8rem;
  background: #fff;
  box-shadow: 0 0 0.16rem rgba(0, 39, 61, 0.05);
  border-radius: 0.08rem;
  opacity: 0;
  -webkit-transform-origin: 50%;
  transform-origin: 50%;
}

.x-alert__body {
  padding: 0.6rem 0.4rem 0.2rem 0.4rem;
}

.x-alert__body > h1 {
  display: block;
  margin-bottom: 0.15rem;
  font-size: 0.34rem;
  line-height: 1.5;
  margin-top: -0.1rem;
  color: #3c3c5a;
  text-align: center;
}

.x-alert__body > .desc {
  font-size: 0.28rem;
  line-height: 1.5;
  color: #8a8a9c;
}

.x-alert__body > .desc.no-title {
  color: #3c3c5a;
}

.x-alert__btns {
  display: flex;
  padding: 0.4rem;
  justify-content: flex-end;
}

.x-alert__btns .x-button {
  font-size: 0.28rem;
}

.x-alert__btns .x-button:not(:last-child) {
  margin-right: 0.2rem;
}

.x-alert--show {
  opacity: 1;
}

.x-alert--show .x-alert__inner {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
  opacity: 1;
  -webkit-animation: __x_alert_show 0.2s ease-out;
  animation: __x_alert_show 0.2s ease-out;
}

@-webkit-keyframes __x_alert_show {
  0% {
    -webkit-transform: scale(1.4, 1.4);
    transform: scale(1.4, 1.4);
    opacity: 0;
  }

  75% {
    -webkit-transform: scale(0.95, 0.95);
    transform: scale(0.95, 0.95);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    opacity: 1;
  }
}

@keyframes __x_alert_show {
  0% {
    -webkit-transform: scale(1.4, 1.4);
    transform: scale(1.4, 1.4);
    opacity: 0;
  }

  75% {
    -webkit-transform: scale(0.95, 0.95);
    transform: scale(0.95, 0.95);
    opacity: 1;
  }

  100% {
    -webkit-transform: scale(1, 1);
    transform: scale(1, 1);
    opacity: 1;
  }
}

.x-alert--hide {
  opacity: 0;
  -webkit-transition: opacity 0.15s ease-out;
  transition: opacity 0.15s ease-out;
  pointer-events: none;
}

.x-alert--hide .x-alert__inner {
  -webkit-transform: scale(0.95, 0.95);
  transform: scale(0.95, 0.95);
  -webkit-transition: all 0.15s ease-out;
  transition: all 0.15s ease-out;
}
.x-aspect-ratio {
  position: relative;
  overflow: hidden;
}

.x-aspect-ratio__inner {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.x-aspect-ratio__ar {
  pointer-events: none;
  width: 100%;
}
.x-safe-area-inset-top {
  position: relative;
  z-index: 99;
  height: constant(safe-area-inset-top);
  height: env(safe-area-inset-top);
  overflow: hidden;
}

.x-safe-area-inset-bottom {
  position: relative;
  z-index: 99;
  height: constant(safe-area-inset-bottom);
  height: env(safe-area-inset-bottom);
  overflow: hidden;
}

.x-safe-area-ios-app {
  min-height: 20px;
}
