  transition: opacity 0.3s ease-out;
}

.x-app-header__inner {
  position: relative;
  z-index: 2;
  flex: 1 1;
  display: flex;
  align-items: center;
  min-height: 1.1rem;
}

.x-app-header__inner a:not([class]),
.x-app-header__inner button:not([class]) {
